html {
  font-size: 20px;
  @include lg {
    font-size: 22px;
  }
}

body {
  font-size: 1rem;
  line-height: 1.5;
  height: 100%;
  position: relative;
  -webkit-font-smoothing: antialiased;
  color: $black;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, roboto,
               'avenir next', avenir,
               helvetica, 'helvetica neue',
               ubuntu, noto,
               'segoe ui', arial,
               sans-serif;
  font-weight: 400;
}

.f1, .f2, .f3, .f4 {
  font-family: -apple-system, BlinkMacSystemFont, roboto,
               'avenir next', avenir,
               helvetica, 'helvetica neue',
               ubuntu, noto,
               'segoe ui', arial,
               sans-serif;
  font-weight: 500;
}

.real {
  font-family: Real Text;
  .f1, .f2, .f3, .f4 {
    font-family: Real Head;
  }
}

::selection {
  background: $selection;
}

p {
  margin-bottom: 1rem;
}

/* Measure is limited to ~66 characters */
.measure {
  max-width: 30rem;
}

/* Measure is limited to ~80 characters */
.measure-wide {
  max-width: 34rem;
}

/* Measure is limited to ~45 characters */
.measure-narrow {
  max-width: 20rem;
}

/* Book paragraph style - paragraphs are indented with no vertical spacing. */
.indent {
  text-indent: 1em;
  margin-top: 0;
  margin-bottom: 0;
}

.small-caps {
  font-variant: small-caps;
}

.truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@include sm {
  .measure-sm  {
    max-width: 30em;
  }
  .measure-wide-sm {
    max-width: 34em;
  }
  .measure-narrow-sm {
    max-width: 20em;
  }
  .indent-sm {
